.footer{
    background: linear-gradient(257deg, #1E0070 0%, #3F0070 100%);

    &__top{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 100px;

        padding: 100px 0;
        border-bottom: 1px dashed rgba(256, 256,256, 0.1);
    }

    &__bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;

        padding: 30px 0;
    }

    &__block:nth-child(1) &__list{
        display: grid;
        grid-template-columns: 1.8fr 1fr;
        grid-gap: 24px 53px;
    }

    &__list{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 24px;
    }

    &__list-item{
        position: relative;
        padding-left: 30px;
        list-style: none;
    }

    &__list-item::before{
        content: '';
        position: absolute;

        top: 7px;
        left: 0;

        width: 15px;
        height: 1px;

        background: #FBFB00;
    }

    &__list-link{
        color: #FFF;
        font-family: Days One;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-decoration: none;
        transition: opacity .4s ease;
    }

    &__list-link:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__block-title{
        margin-bottom: 30px;

        color: #F27FFF;
        font-family: Days One;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__block:nth-child(3) {
        max-width: 736px;
        width: 100%;
    }

    &__contacts{
        padding: 48px 30px 48px 40px;
        border-radius: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.10);
        background: rgba(13, 0, 48, 0.4);
        backdrop-filter: blur(10px);
    
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px;
    }

    &__contact h4{
        color: #FFF;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-bottom: 10px;
    }

    &__contacts ul{
        position: relative;
        padding-left: 22px;
    }

    &__contacts ul::before{
        content: url('../../../img/icons/yellow-phone.svg');
        position: absolute;
        left: 0;
        top: 6px;
    }

    &__contacts li{
        margin-bottom: 5px;
        list-style: none;
    }

    &__contacts li:last-child{
        margin-bottom: 0;
    }

    &__contacts li a{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 187.5% */

        text-decoration: none;

        transition: opacity .4s ease
    }

    &__contacts li a:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__contacts li a span{
        font-weight: 800;
    }

    &__bottom-block{
        display: flex;
        align-items: flex-end;
        gap: 15px;
    }
    
    &__bottom-block p{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px; /* 228.571% */
    }
}



@media(max-width: 1440px) {
    .footer{
        &__top{
            padding: 80px 0;
            gap: 80px;
        }

        &__contacts{
            padding: 30px;
            grid-gap: 20px;
        }

        &__block:nth-child(1) &__list{
            grid-gap: 24px 30px;
        }

        &__block:nth-child(3) {
            max-width: 690px;
        }

        &__contact h4{
            font-size: 16px;
        }
    }
}

@media(max-width: 1200px) {
    .footer{

        &__top{
            padding: 50px 0 40px;
        }

        &__block:nth-child(3) {
            max-width: 520px;
        }

        &__contacts{
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 30px 20px;
        }
    }
}

@media(max-width: 1024px) {
    .footer{
        &__top{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 50px;
        }

        &__block:nth-child(3) {
            grid-column: span 2;
            max-width: 100%;
        }

        &__block:nth-child(1) &__list{
            grid-template-columns: 1.5fr 1fr;
        }

        &__block-title{
            margin-bottom: 20px;
        }

        &__contacts{
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 30px;
        }
    }
}

@media(max-width: 768px) {
    .footer{
        &__top{
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 40px;
        }

        &__block:nth-child(1) {
            border-bottom: 1px dashed rgba(256, 256, 156, 0.1);
            padding-bottom: 40px;
        }

        &__block:nth-child(1) &__list{
            grid-gap: 20px 30px;
        }

        &__list{
            grid-gap: 20px;
        }

        &__block:nth-child(3) {
            display: none;
        }

        &__block-title{
            text-align: center;
        }

        &__bottom{
            flex-direction: column;
            align-items: center;
            gap: 10px;
        }

        &__bottom-block p{
            text-align: center;
            line-height: 24px;
        }

        &__bottom-block{
            align-items: center;
        }

        &__list-item{
            padding-left: 23px;
        }

        &__list-item::before{
            width: 10px;
        }

        &__list-link{
            font-size: 12px;
        }
    }
}