.order-modal{
    &__inner{
        max-width: 490px;
        width: 100%;
    }
}

@media(max-width: 1200px) {
    .order-modal{
        &__inner{
            max-width: 400px;
        }
    }
}

@media(max-width: 475px) {
    .order-modal{
        &__inner{
            max-width: 320px;
        }
    }
}