h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
}

body{
    counter-reset: section;
}

.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}

main {
    flex: 1 0 auto;
}

footer {
    flex: 0 0 auto;
}

.container{
    max-width: 1440px;
    padding: 0 10px;
    margin: 0 auto;
}

.section-title{
    color:#160052;
    font-family: Days One;
    font-size: 48px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.modal{
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    opacity: 0;
    visibility: hidden;
    transition: opacity .4s ease;


    &.visible{
        opacity: 1;
        visibility: visible;
        z-index: 999;
        transition: opacity .4s ease;
    }

    &__overlay{
        width: 100%;
        height: 100%;
    
        display: flex;
        align-items: center;
        justify-content: center;
    
        background: rgba(22, 0, 82, 0.8);
    }

    &__inner{
        position: relative;

        border-radius: 30px;
        background: #FFF;

        padding: 50px 45px;

        box-sizing: border-box;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__close-button{
        position: absolute;
        top: 30px;
        right: -27px;

        width: 55px;
        height: 55px;

        display: flex;
        align-items: center;
        justify-content: center;
        

        background: #fff;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.15);
        border-radius: 50%;

        outline: none;
        cursor: pointer;
        padding: 0;
        margin: 0;
        border: none;
    }

    &__title{
        color: #160052;
        text-align: center;
        font-family: Days One;
        font-size: 38.119px;
        font-style: normal;
        font-weight: 400;
        line-height: 43.877px; /* 115.104% */

        margin-bottom: 13px;
    }

    &__subtitle{
        color: #160052;
        font-family: Nunito;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;

        margin-bottom: 33px;
    }

    &__input-wrap{
        margin-bottom: 30px;
    }

    &__input-label{
        color: #160052;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;

        margin-bottom: 7px;
    }

    &__input{
        width: 100%;
    }

    &__input input{
        width: 100%;
        outline: none;
        height: 50px;
        border: none;

        border-radius: 15px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        background: #F7F6FA;

        color: #160052;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;

        padding: 0 20px;

        box-sizing: border-box;
    }

    &__submit{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;

        width: 100%;
        height: 70px;

        padding: 0 15px;

        cursor: pointer;
        outline: none;
        border: none;
        border-radius: 60px;
        background: #FBFB00;

        transition: opacity .4s ease;

        margin: 40px 0 30px;
    }

    &__submit:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__submit-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__submit-text{
        color: #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__warning{
        color: #160052;
        text-align: center;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px; /* 150% */
        opacity: 0.7;

        max-width: 320px;
    }

    &__warning a{
        color: #160052;
        font-family: Nunito;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px;
        text-decoration: none;

        position: relative;
    }

    &__warning a::before{
        content: '';
        position: absolute;

        left: 50%;
        transform: translateX(-50%);
        bottom: 0;

        width: 100%;
        height: 1px;
        background: rgba(22, 0, 82, 0.2);
    }

    &__image img{
        width: 100%;
        height: 100%;
    }
}

@media(max-width: 1200px) {
    .section-title{
        font-size: 44px;
        line-height: 48px;
    }

    .modal{
        &__inner{
            padding: 30px;
        }

        &__title{
            font-size: 32px;
            line-height: 38px;
            margin-bottom: 5px;
        }

        &__subtitle{
            font-size: 18px;
            line-height: 24px;
            margin-bottom: 25px;
        }

        &__close-button{
            top: 20px;

            width: 45px;
            height: 45px;

            right: -25px;
        }

        &__input-wrap{
            margin-bottom: 20px;
        }

        &__submit{
            height: 60px;
            margin: 30px 0 20px;
        }

        &__submit-text{
            font-size: 16px;
        }
    }
}

@media(max-width: 1024px) {
    .section-title{
        font-size: 40px;
        line-height: 44px;
    }
}

@media(max-width: 768px) {
    .section-title{
        font-size: 38px;
        line-height: 42px;
    }

    .modal{
        &__inner{
            padding: 30px;
        }

        &__title{
            font-size: 30px;
            line-height: 36px;
            margin-bottom: 3px;
        }

        &__subtitle{
            line-height: 24px;
        }

        &__submit{
            height: 50px;
            margin: 20px 0;
        }

        &__submit-text{
            font-size: 14px;
        }
    }
}

@media(max-width: 475px) {
    .section-title{
        font-size: 36px;
        line-height: 40px;
    }

    .modal{
        &__inner{
            padding: 30px 15px;
        }

        &__title{
            font-size: 26px;
            line-height: 30px;
            margin-bottom: 3px;
        }

        &__subtitle{
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 20px;
        }

        &__close-button{
            top: -20px;
            right: 0;
        }
    }
}