.products-slider{
    margin-bottom: 120px;
    
    &__inner{
        padding-bottom: 120px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &.novelty-slider &__inner{
        padding-bottom: 0;
        border: none;
    }

    &.extra-slider &__inner{
        padding-bottom: 0;
        border: none;
    }

    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;

        margin-bottom: 40px;
    }

    &__arrows{
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__arrow{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 60px;
        height: 60px;

        border-radius: 50%;
        outline: none;
        cursor: pointer;
        background: #fff;
        border: 2px solid #F27FFF;

        transition: opacity .4s ease;

    }

    &__arrow:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__swiper{
        border-radius: 20px;
    }
}

@media(max-width: 1200px) {
    .products-slider{
        margin-bottom: 100px;

        &__inner{
            padding-bottom: 100px;
        }
    }
}

@media(max-width: 1024px) {
    .products-slider{
        margin-bottom: 80px;

        &__inner{
            padding-bottom: 80px;
        }
    }
}

@media(max-width: 768px) {
    .products-slider{
        &__top{
            margin-bottom: 30px;
        }
    }
}

@media(max-width: 475px) {
    .products-slider{
        margin-bottom: 60px;

        &.novelty-slider &__inner{
            padding-bottom: 80px;
        }

        &.extra-slider &__inner{
            padding-bottom: 80px;
        }

        &__inner{
            position: relative;
            padding-bottom: 140px;
        }

        &__top{
            margin-bottom: 20px;
        }

        &__arrows{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 60px;
        }

        &.novelty-slider &__arrows{
            bottom: 0;
        }

        &.extra-slider &__arrows{
            bottom: 0;
        }

        &__top{
            justify-content: center;
        }


    }
}