.hero{
    background: linear-gradient(257deg, #1E0070 0%, #3F0070 100%);
    padding-bottom: 20px;
    margin-bottom: 130px;

    &-slider__slide-image{
        width: 100%;
        height: 600px;
    }

    &-slider__slide-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
    }

    &-slider{
        margin-bottom: 20px;
        border-radius: 20px;
    }

    &-slider__button{
        cursor: pointer;

        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        z-index: 2;

        outline: none;
    }

    &-slider__button--prev{
        left: 40px;
    }

    &-slider__button--next{
        right: 40px;
    }

    &__triggers{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
    }

    &__trigger{
        border-radius: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.10);
        background: rgba(13, 0, 48, 0.4);
        backdrop-filter: blur(10px);

        padding: 30px;
        
        display: flex;
        align-items: center;
        gap: 30px;
    }

    &__trigger-title{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: 900;
        line-height: normal;

        margin-bottom: 3px;
    }

    &__trigger-text{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    .products-slider{
        display: none;

        margin-bottom: 30px;

        &__inner{
            padding-bottom: 0;
            border-bottom: none;
        }

        .container{
            padding: 0;
            max-width: 100%;
        }

        &__top{
            margin-bottom: 20px;
        }

        &__arrows{
            gap: 25px;
        }

        &__arrow{
            height: 27px;
            width: 17px;
            border-radius: 0;

            border: none;
            background: transparent;
        }

        &__arrow svg{
            width: 100%;
            height: 100%;
        }

        &__arrow svg path{
            stroke: #F27FFF;
        }

        &__title{
            color: #FFF;
            text-align: center;
            font-family: Days One;
            font-size: 32px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }
    }
}

@media(max-width: 1440px) {
    .hero{

        &-slider__button--prev{
            left: 20px;
        }

        &-slider__button--next{
            right: 20px;
        }
    }
}

@media(max-width: 1200px) {
    .hero{
        margin-bottom: 100px;

        &__triggers{
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 15px;
        }

        &-slider__slide-image{
            height: 500px;
        }

        &-slider__button--prev{
            left: 10px;
        }

        &-slider__button--next{
            right: 10px;
        }
    }
}

@media(max-width: 1024px) {
    .hero{
        margin-bottom: 80px;

        &-slider__slide-image{
            height: 400px;
        }
    }
}

@media(max-width: 768px) {
    .hero{
        &-slider{
            margin-bottom: 30px;
        }

        &-slider__slide-image{
            height: 300px;
        }

        &-slider__button{
            display: none;
        }

        &__triggers{
            grid-gap: 10px;
        }

        &__trigger{
            gap: 20px;
        }

        &__trigger-title{
            font-size: 16px;
        }

        &__trigger-text{
            font-size: 16px;
        }

        .products-slider{
            display: block;
        }
    }
}

@media(max-width: 576px) {
    .hero{
        &-slider__slide-image{
            height: 260px;
        }

        &__trigger{
            flex-direction: column;
            align-items: center;
            gap: 15px;
        }

        &__trigger-content{
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}

@media(max-width: 475px) {
    .hero{
        margin-bottom: 60px;
        padding-bottom: 15px;

        &-slider__slide-image{
            height: 200px;
        }

        &__trigger-title{
            font-size: 14px;
        }

        &__trigger-text{
            font-size: 14px;
            text-align: center;
        }

        &__trigger{
            padding: 20px 11px;
        }

        .products-slider{
            &__top{
                justify-content: space-between;
            }

            &__arrows{
                position: static;
                transform: translateX(0);
            }
        }
    }
}