.cataloge{
    margin-bottom: 120px;

    &__inner{
        padding-bottom: 100px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 40px;
    }

    &__items{
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-gap: 20px;
    }

    &__item{
        height: 280px;

        display: flex;
        flex-direction: column;
        align-items: center;

        text-decoration: none;

        border-radius: 20px;
        background: #FBFB00;

        padding: 10px 20px 30px;
    }

    &__item:hover &__item-image{
        transform: scale(1.1);
        transition: transform .4s ease;
    }

    &__item-title{
        color: #160052;
        text-align: center;
        font-family: Nunito, sans-serif;
        font-size: 22px;
        font-style: normal;
        font-weight: 900;
        line-height: 24px; /* 109.091% */

        margin-top: auto;

        min-height: 48px;

        display: flex;
        align-items: center;
    }

    &__item-image{
        width: 160px;
        height: 160px;
        transition: transform .4s ease;

        margin-bottom: 20px;
    }

    &__item-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .ui-button{
        max-width: 220px;
    }
}

@media(max-width: 1440px) {
    .cataloge{
        &__items{
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 15px;
        }

        &__item{
            height: 270px;
        }
    }
}

@media(max-width: 1200px) {
    .cataloge{
        margin-bottom: 100px;
    }
}

@media(max-width: 1024px) {
    .cataloge{
        margin-bottom: 80px;

        &__inner{
            padding-bottom: 80px;
        }
    }
}

@media(max-width: 768px) {
    .cataloge{
        &__top{
            margin-bottom: 30px;
        }

        &__items{
            grid-gap: 10px;
        }

        &__item{
            padding: 10px 20px 20px;
            height: auto;
        }

        &__item-title{
            font-size: 20px;
        }

        &__item-image{
            width: 130px;
            height: 130px;
        }
    }
}

@media(max-width: 576px) {
    .cataloge{
        &__items{
            grid-template-columns: repeat(2, 1fr);
        }

        &__item-image{
            width: 110px;
            height: 110px;
        }

        &__item-title{
            font-size: 18px;
            line-height: 20px;
        }
    }
}

@media(max-width: 475px) {
    .cataloge{
        margin-bottom: 60px;

        &__top{
            margin-bottom: 20px;
            justify-content: center;
        }

        &__inner{
            position: relative;
            padding-bottom: 140px;
        }

        .ui-button{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 60px;

            max-width: 100%;
        }

        .section-title{
            text-align: center;
        }
    }
}