.cataloge-modal{
    position: fixed;
    top: 0;
    right: 0;

    width: 100%;
    height: 100%;

    opacity: 0;
    visibility: hidden;
    transition: opacity .4s ease;
    
    &.-js-visible{
        opacity: 1;
        visibility: visible;
        transition: opacity .4s ease;
        z-index: 999;
    }

    &__overlay{
        background: rgba(22, 0, 82, 0.8);

        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__inner{
        max-width: 1420px;
        width: 100%;
        padding: 0 10px;

        display: flex;
        justify-content: center;
    }

    &__products{
        max-width: 1100px;
        width: 100%;
        border-radius: 20px;
        background: #FFF;
        padding: 5px;

        display: grid;
        grid-template-columns: 320px 1fr;
        
    }

    &__aside{
        padding: 18px 35px;
        background: rgba(243, 242, 246, 0.4);
        border-radius: 15px;
    }

    &__aside.-js-hide{
        display: none;
    }

    &__type{
        padding-bottom: 15px;
        margin-bottom: 15px;
        border-bottom: 1px dashed rgba(30, 0, 112, 0.1);
        list-style: none;
    }

    &__type:last-child{
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
    }

    &__type-link{
        display: flex;
        align-items: center;
        text-decoration: none;
        gap: 20px;

        opacity: .7;
        transition: opacity .4s ease;
    }

    &__type-link.-js-hovered{
        opacity: 1;
        transition: opacity .4s ease;
    }

    &__type-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__type-title{
        color: #160052;
        font-family: Days One;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__type-arrow{
        display: flex;
        align-items: center;    
        justify-content: center;
        margin-left: auto;
    }

    &__content{
        padding: 15px 0;
    }

    &__content.-js-visible{
        display: block;
    }

    &__content-wrapper{
        height: 546px;
        overflow-y: auto;
        padding: 30px 65px 30px 50px;

        scrollbar-width: 5px;
        scrollbar-color: #F27FFF transparent;
    }

    &__content-wrapper::-webkit-scrollbar {
        width: 5px;
    }
      
    &__content-wrapper::-webkit-scrollbar-track {
        background: transparent;
    }
      
    &__content-wrapper::-webkit-scrollbar-thumb {
        background-color: #F27FFF;
        border-radius: 20px;
        border: none; 
    }

    &__content-item{
        display: none;
    }

    &__content-item.-js-visible{
        display: block;
    }

    &__lists{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: flex-start;
        grid-gap: 50px;
    }
    
    &__list h3{
        color: #F27FFF;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-bottom: 15px;
    }

    &__list li{
        margin-bottom: 10px;
        list-style: none;
    }

    &__list li:last-child{
        margin-bottom: 0;
    }

    &__list li a{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.2; /* 187.5% */
        transition: opacity .4s ease;
        text-decoration: none;
    }

    &__list li a:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__content-top{
        display: none;
        align-items: center;
        justify-content: center;

        padding-bottom: 20px;
        border-bottom: 1px dashed rgba(30, 0, 112, 0.1);

        position: relative;
        padding-left: 20px;

        margin-bottom: 30px;
    }

    &__content-title{
        color: #160052;
        font-family: Days One;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__return-button{
        display: flex;
        align-items: center;
        justify-content: center;

        outline: none;
        cursor: pointer;
        padding: 0;
        margin: 0;
        background: transparent;
        border: none;

        position: absolute;
        left: 0;
        top: 0;

        width: 10px;
        height: 20px;
    }
}


@media(max-width: 1400px) {
    .cataloge-modal{
        &__lists{
            grid-gap: 40px;
        }
    }
}

@media(max-width: 1024px) {
    .cataloge-modal{
        &__content-wrapper{
            padding: 0 20px;
        }

        &__content-top{
            display: flex;
        }

        &__content{
            display: none;
        }

        &__products{
            padding: 0;
            grid-template-columns: 1fr;
        }

        &__aside{
            max-width: 100%;
            padding: 20px 30px;
        }

        &__type-link{
            opacity: 1;
        }

        &__lists{
            grid-gap: 35px;
        }
    }
}

@media(max-width: 991px) {
    .cataloge-modal{
        &__lists{
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media(max-width: 576px) {
    .cataloge-modal{
        &__lists{
            grid-template-columns: repeat(1, 1fr);
        }
    }
}