.map{
    position: relative;
    height: 900px;

    .container{
        height: 100%;
    }

    &__inner{
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
    

    &__container{
        height: 100%;
        width: 100%;

        position: absolute;
        top: 0;
        left: 0;

        z-index: 1;

        width: 100%;
        
    }

    &__aside{
        max-width: 380px;
        width: 100%;

        position: relative;
        z-index: 2;
    }

    &__tabs{
        padding: 40px 50px;
        border-radius: 20px;
        background: linear-gradient(257deg, #1E0070 0%, #3F0070 100%);
    }
}

.map-tab{
    padding-bottom: 40px;
    margin-bottom: 40px;
    border-bottom: 1px dashed rgba(256, 256, 256, 0.2);

    cursor: pointer;
    opacity: .3;

    &.-js-active{
        opacity: 1;
    }

    &:last-child{
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
    }

    &__title{
        color: #F27FFF;
        font-family: Days One;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-bottom: 15px;
    }

    &__wrapper{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    &__contacts{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;
        position: relative;
        padding-left: 20px;
    }

    &__contacts::before{
        content: url('../img/icons/yellow-phone.svg');
        position: absolute;
        left: 0;
        top: 6px;
    }

    &__contacts a{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 150% */
        text-decoration: none;
    }

    &__contacts a span{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 800;
        line-height: 30px; /* 150% */
    }

    &__arrow{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__arrow svg path{
        transition: opacity .4s ease;
    }

    &:hover &__arrow svg path{
        opacity: 1;
        transition: opacity .4s ease;
    }
}

.ymaps-2-1-79-balloon__layout{
    overflow: visible !important;
}

.ymaps-2-1-79-balloon__close-button{
    width: 55px !important;
    height: 55px !important;
    border-radius: 50% !important;

    background-image: url('../img/icons/cross.svg') !important;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;

    background-color: #FFF !important;
    filter: drop-shadow(0px 4px 24px rgba(0, 0, 0, 0.15)) !important;
    opacity: 1 !important;

    position: relative !important;
    top: -30px !important;
    right: -30px !important;
}

.ymaps-2-1-79-balloon__content{
    width: 290px;
    margin: 0 !important;
    box-sizing: border-box !important;

    padding: 40px !important;

    border-radius: 10px !important;
    background: linear-gradient(257deg, #1E0070 0%, #3F0070 100%) !important;
}

.balloon{

    &__address-text{
        color: #F27FFF;
        font-family: Days One;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    
        margin-bottom: 15px;
        max-width: 190px;
    }
    
    &__contacts{
        position: relative;
        padding-left: 24px;
        margin-bottom: 20px;

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;
    }
    
    &__contacts::before{
        content: url('../img/icons/yellow-phone.svg');
        position: absolute;
    
        left: 0;
        top: 5px;
    }
    
    &__contact{
        color:#FFF;
        font-family: Nunito;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px; /* 150% */   
        text-decoration: none;
    }

    &__checkbox{
        max-width: 210px;
    }
    
    &__checkbox input{
        display: none;
    }
    
    &__checkbox label{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;
    
        width: 100%;
        height: 50px;
    
        border-radius: 60px;
        background:#FBFB00;
    
        cursor: pointer;
        transition: opacity .4s ease;
    }
    
    &__checkbox label:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }
    
    &__checkbox-text{
        color: #160052;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;

        
    }
    
    &__checkbox-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }
}


@media(max-width: 1440px) {
    .map{
        height: 800px;

        &__tabs{
            padding: 30px 40px;
        }
    }

    .map-tab{
        margin-bottom: 30px;
        padding-bottom: 30px;
    }

    .ymaps-2-1-79-balloon__content{
        padding: 25px !important;
        width: 225px;
    }

    .balloon__checkbox label{
        height: 40px;
    }

    .balloon__address-text{
        font-size: 18px;
    }

    .ymaps-2-1-79-balloon__close-button{
        width: 40px !important;
        height: 40px !important;

        top: -20px !important;
        right: -20px !important;
    }

    .balloon__contacts{
        padding-left: 18px;
    }

    .balloon__contacts::before{
        top: 3px;
    }

    .balloon__contact{
        font-size: 16px;
        line-height: 24px;
    }
}

@media(max-width: 1024px) {
    .map{
        height: 700px;

        &__aside{
            max-width: 360px;
        }
    }

    .map-tab{
        margin-bottom: 20px;
        padding-bottom: 20px;

        &__title{
            font-size: 20px;
        }

        &__contacts a,
        &__contacts a span{
            font-size: 18px;
            line-height: 26px;
        }
    }
}

@media(max-width: 991px) {
    .map{
        &.contacts-page{
            height: auto;
            padding-bottom: 730px;
        }

        &.contacts-page &__container{
            height: 700px;
            top: auto;
            bottom: 0;
        }

        &.contacts-page &__aside{
            max-width: 100%;
        }

        &.contacts-page &__tabs{
            max-width: 100%;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 20px;

            padding: 30px;
        }

        &.contacts-page &__tab{
            padding-bottom: 0;
            border-bottom: none;
        }
    }

    .map-tab{
        margin-bottom: 0;

        &__arrow{
            display: none;
        }
    
        &__title{
            margin-bottom: 10px;
            font-size: 18px;
        }

        &__contacts::before{
            top: 3px;
        }

        &__contacts a,
        &__contacts a span{
            font-size: 16px;
            line-height: 24px;
        }
    }
   
    
}

@media(max-width: 768px) {
    .map{
        &__aside{
            display: none;
        }

        &.contacts-page{
            padding-bottom: 630px;
        }

        &.contacts-page &__container{
            height: 600px;
        }

        &.contacts-page &__aside{
            display: block;
        }

        &.contacts-page &__tabs{
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 40px;
        }
    }

    .map-tab{

        &:last-child{
            grid-column: span 2;
        }

        &__title{
            font-size: 22px;
            margin-bottom: 12px;
        }

        &__contacts a,
        &__contacts a span{
            font-size: 18px;
            line-height: 26px;
        }
    }
}

@media(max-width: 576px) {
    .map{
        &.contacts-page &__tabs{
            grid-gap: 30px;
        }

    }
    .map-tab{
    
        &__title{
            margin-bottom: 10px;
            font-size: 18px;
        }

        &__contacts::before{
            top: 3px;
        }

        &__contacts a,
        &__contacts a span{
            font-size: 16px;
            line-height: 24px;
        }
    }
}

@media(max-width: 475px) {
    .map-tab{

        &__contacts{
            padding-left: 0;
        }

        &__contacts::before{
            display: none;
        }
    }
}

@media(max-width: 425px) {
    .map{
        &.contacts-page &__tabs{
            grid-template-columns: 1fr;
        }

        &.contacts-page &__tab:last-child{
            grid-column: span 1;
        }
    }

    .map-tab{
        &__arrow{
            display: block;
        }
    }
}