.about-us{
    padding: 152px 0;
    background-image: url('../img/backgrounds/about-us/about-us-bg.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 50px;
    }

    &__content{
        max-width: 490px;
        position: relative;
        z-index: 1;
    }

    &__image{
        max-width: 826px;
        width: 100%;
        height: 660px;

        position: relative;
    }

    &__image::before{
        content: '';
        position: absolute;

        height: 540px;
        width: 707px;

        background-image: url('../img/about-us/decor.png');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;

        bottom: 0;
        left: -190px;
    }

    &__image img{
        width: 100%;
        height: 100%;   
        object-fit: cover;
        border-radius: 20px;
    }

    &__content h2{
        margin-bottom: 30px;
    }

    &__text{
        margin-bottom: 50px;
    }

    &__text p{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 28px; /* 175% */
        margin-bottom: 20px;
    }

    &__text p:last-child{
        font-weight: 700;
        margin-bottom: 0;
    }

    &__button{
        max-width: 280px;
        width: 100%;
        height: 70px;

        border-radius: 40px;
        background: #F27FFF;

        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;

        text-decoration: none;
        transition: opacity .4s ease;
    }

    &__button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__button-text{
        color: #160052;
        font-family: Days One;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media(max-width: 1440px) {
    .about-us{
        padding: 120px 0;

        &__image::before{
            height: 480px;
            width: 630px;
            left: -170px;
        }
    }
}

@media(max-width: 1200px) {
    .about-us{
        padding: 100px 0;

        &__image{
            height: 500px;
        }

        &__image::before{
            height: 360px;
            width: 480px;
            left: -130px;
        }

        &__text{
            margin-bottom: 30px;
        }
    }
}

@media(max-width: 1024px) {
    .about-us{
        padding: 80px 0;

        &__inner{
            flex-direction: column;
            align-items: center;
        }

        &__content{
            max-width: 100%;
        }

        &__content h2{
            margin-bottom: 20px;
        }

        &__content p{
            margin-bottom: 10px;
        }

        &__image{
            height: 400px;
            max-width: 720px;
        }
    }
}

@media(max-width: 768px) {
    .about-us{
        padding: 60px 0;

        &__button{
            height: 60px;
        }

        &__image{
            overflow: hidden;
        }

        &__image::before{
            height: 260px;
            width: 340px;
            left: -100px;
        }
    }
}

@media(max-width: 475px) {
    .about-us{
        padding: 40px 0 20px;

        &__inner{
            gap: 40px;
        }

        &__content h2{
            margin-bottom: 15px;
        }

        &__content p{
            font-size: 14px;
            line-height: 20px;
        }

        &__button{
            max-width: 100%;
        }

        &__image{
            height: 300px;
        }

        &__image::before{
            height: 210px;
            width: 280px;
            left: -80px;
        }
    }
}

@media(max-width: 425px) {
    .about-us{
        &__image{
            height: 260px;
        }

        &__image::before{
            height: 200px;
            width: 260px;
            left: -70px;
        }
    }
}