.search-form{
    width: 100%;
    
    &__inner{
        position: relative;
        width: 100%;
    }

    &__input{
        width: 100%;
    }

    &__input input{
        width: 100%;
        height: 40px;
        border-radius: 15px;
        background: #FFF;

        padding: 0 100px 0 20px;

        border: none;
        outline: none;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        box-sizing: border-box;
    }

    &__input input::placeholder{
        color: rgba(22, 0, 82, 0.5);
        font-family: Nunito, sans-serif;
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__submit{
        position: absolute;
        right: 20px;
        top: 0;

        height: 40px;

        display: flex;
        align-items: center;
        gap: 10px;

        background: transparent;
        cursor: pointer;
        border: none;
        padding: 0;
        margin: 0;

        transition: opacity .4s ease;
    }

    &__submit:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__submit-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__submit-text{
        color: #F27FFF;
        font-family: Days One;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.search-products{
    position: absolute;
    left: 0;
    top: 150%;

    width: 100%;

    border-radius: 15px;
    background: #FFF;
    box-shadow: 0px 34px 84px 0px rgba(0, 0, 0, 0.55);

    padding: 20px 0 20px 30px;

    opacity: 0;
    visibility: hidden;
    transition: opacity .4s ease;

    &.-js-visible{
        opacity: 1;
        visibility: visible;
        z-index: 2;
        transition: opacity .4s ease;
    }

    &__items{
        height: 360px;
        overflow-y: auto;
        padding-right: 30px;

        scrollbar-width: 5px;
        scrollbar-color: #F27FFF transparent;
    }

    &__items::-webkit-scrollbar {
        width: 5px;
    }
      
    &__items::-webkit-scrollbar-track {
        background: transparent;
    }
      
    &__items::-webkit-scrollbar-thumb {
        background-color: #F27FFF;
        border-radius: 20px;
        border: none; 
    }

    &__item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 15px;

        text-decoration: none;

        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px dashed rgba(30, 0, 112, 0.1);

        transition: opacity .4s ease;

    }

    &__item:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__item:last-child{
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
    }

    &__item-image{
        width: 60px;
        height: 60px;
    }

    &__item-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__item-title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 16px; /* 114.286% */

        max-width: 164px;
    }

    &__item-price{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 4px;
    }

    &__item-current-price{
        color: #160052;
        font-family: Days One;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__item-old-price{
        color: #160052;
        font-family: Days One;
        font-size: 11px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        position: relative;
    }

    &__item-old-price::before{
        content: '';
        position: absolute;

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        
        width: 100%;
        height: 1px;
        background: #F27FFF;
    }

    &__button{
        display: inline-block;
        color: #F27FFF;
        text-align: center;
        font-family: Days One;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.2;
        margin-top: 22px;

        transition: opacity .4s ease;
        text-decoration: none;
    }

    &__button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }
}

.header{
    padding: 40px 0;
    background: linear-gradient(257deg, #1E0070 0%, #3F0070 100%);

    top: -100px;
    transition: all .4s ease;
    
    &.-js-fixed{
        padding: 20px 0;

        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
    
        z-index: 999;

        transition: all .4s ease;
    }

    &.-js-fixed &__wrapper-top{
        display: none;
    }

    &.-js-fixed &__menu-button{
        display: flex;
    }

    &.-js-fixed &__logo{
        max-width: 160px;
        width: 100%;

        margin-right: 20px;
    }

    &.-js-fixed &__container{
        gap: 0;
        border-bottom: none;
    }

    &.-js-fixed &__wrapper{
        max-width: 790px;
        margin-right: 50px;
    }

    &.-js-fixed &__contacts{
        gap: 1px;
        max-width: 128px;
        margin-right: 50px;
    }

    &.-js-fixed &__contacts-phone{
        font-size: 18px;
    }

    &.-js-fixed &__cart{
        margin-left: auto;
        max-width: 195px;
    }

    &.-js-fixed &__cart-icon{
        width: 40px;
        height: 40px;
    }

    &.-js-fixed &__cart-title{
        font-size: 16px;
        margin-bottom: 1px;
    }

    &__logo{
        flex-shrink: 0;
    }
    
    &__logo img{
        width: 100%;
        height: 100%;
    }

    &__container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 50px;
    }

    &__wrapper{
        max-width: 640px;
        width: 100%;
        flex-shrink: 1;
    }

    &__wrapper-top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 15px;

        transition: all .4s ease;
    }

    &__wrapper-bottom{
        display: flex;
        align-items: center;
        gap: 16px;
    }

    &__cataloge-button{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        border-radius: 15px;
        background: #FBFB00;
        
        outline: none;
        cursor: pointer;
        padding: 0;
        border: none;
        
        max-width: 210px;
        width: 100%;
        height: 40px;

        transition: opacity .4s ease;
    }

    &__cataloge-button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__cataloge-button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__cataloge-button-text{
        color: #160052;
        font-family: Days One;
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__menu-button{
        display: none;
        align-items: center;
        justify-content: center;
        gap: 11px;

        max-width: 138px;
        width: 100%;
        height: 40px;

        background: transparent;
        border-radius: 15px;
        border: 2px solid #FBFB00;

        outline: none;
        cursor: pointer;
        padding: 0;
        margin: 0;  
        transition: opacity .4s ease;
    }

    &__menu-button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__menu-button-text{
        color: #FBFB00;
        font-family: Days One;
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__menu-button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__menu-button-icon svg path{
        stroke: #FBFB00;
    }

    &__nav-list{
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__nav-item{
        list-style: none;
    }

    &__nav-link{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        text-decoration: none;
        transition: opacity .4s ease;
    }

    &__nav-link:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__contacts{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;

        max-width: 135px;
        width: 100%;
    }

    &__contacts-phone{
        position: relative;
        padding-left: 24px;

        color: #FFF;
        font-family: Days One;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        text-decoration: none;
        transition: opacity .4s ease;
    }

    &__contacts-phone:hover{
        transition: opacity .4s ease;
        opacity: .7;
    }

    &__contacts-phone::before{
        content: url('../../../img/icons/phone.svg');
        position: absolute;

        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }

    &__contacts-button{
        color: #F27FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 900;
        line-height: normal;

        background: transparent;
        outline: none;
        cursor: pointer;
        padding: 0;
        margin: 0;
        border: none;

        transition: opacity .4s ease;

        position: relative;
        padding-bottom: 2px;
    }

    &__contacts-button::after{
        content: '';
        position: absolute;

        width: 100%;
        height: 1px;

        left: 50%;
        transform: translateX(-50%);
        bottom: 0;

        background: rgba(242, 127, 255, 0.5);
    }

    &__contacts-button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__cart{
        display: flex;
        align-items: center;
        gap: 20px;

        max-width: 215px;
        width: 100%;
        text-decoration: none;

        transition: opacity .4s ease;
    }

    &__cart:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__cart-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__cart-icon img{
        width: 100%;
        height: 100%;
    }

    &__cart-title{
        color: #FFF;
        font-family: Days One;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__cart-text{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }

    &__cart-text span:nth-child(2){
        font-weight: 900;
        position: relative;
        padding-bottom: 2px;
    }
    &__cart-text span:nth-child(2):after{
        content: '';
        position: absolute;

        width: 100%;
        height: 1px;

        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        background: rgba(256, 256, 256, 0.5);
    }

    &__search{
        max-width: 410px;
        width: 100%;

        position: relative;
    }

    &__nav-button{
        display: none;

        align-items: center;    
        justify-content: center;
        gap: 7px;

        padding: 0;
        outline: none;
        margin: 0;
        background: transparent;
        border: none;
    }

    &__nav-button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__nav-button-text{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;
    }

    &__search-button{
        display: none;

        align-items: center;    
        justify-content: center;
        gap: 5px;

        padding: 0;
        outline: none;
        margin: 0;
        background: transparent;
        border: none;
    }

    &__search-button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__search-button-text{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;
    }

    &__phone{
        display: none;

        align-items: center;
        justify-content: center;
        gap: 6px;

        text-decoration: none;
    }

    &__phone-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__phone-text{
        color:#FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;
    }

    &__location{
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &__location-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__location-text{
        color: #FFF;
        font-family: Nunito, sans-serif;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;
    }

    &__nav-item:last-child{
        display: none;
    }

    &__search-close{
        display: none;
        align-items: center;
        justify-content: center;
    }
}


@media(max-width: 1440px) {
    .header{
        padding: 30px 0;

        &__nav-button{
            display: flex;
            cursor: pointer;
        }

        &__nav{
            display: none;
        }

        &__cataloge-button{
            max-width: 180px;
        }

        &__search{
            max-width: 100%;
        }

        &__contacts-phone{
            font-size: 18px;
        }

        &__contacts-phone::before{
            top: 60%;
        }

        &__contacts-button{
            line-height: 1;
        }

        &__cart{
            gap: 10px;
            max-width: 195px;
        }

        &__cart-icon{
            width: 50px;
            height: 50px;
        }

        &__cart-title{
            font-size: 20px;
        }

        &__logo{
            max-width: 260px;
        }

        &.-js-fixed &__wrapper {
            margin-right: 20px;
        }

        &.-js-fixed &__contacts {
            margin-right: 20px;
        }

        &.-js-fixed &__cart {
            max-width: 185px;
        }

        &.-js-fixed &__container{
            padding: 0;
            background: transparent;
        }

        &.-js-fixed &__cataloge-button{
            position: static;
            transform: translateX(0);
            max-width: 210px;
        }

        &.-js-fixed &__inner{
            padding-bottom: 0;
        }
    }

    .search-form{

        &__submit{
            right: 0;
            width: 40px;

            display: flex;
            align-items: center;
            justify-content: center;

            border-radius: 15px;
        }

        &__submit-text{
            display: none;
        }

        &__input input{
            padding: 0 40px 0 15px;
        }
    }



    .search-products{
        padding-left: 10px;
    }

    .search-products__items{
        padding-right: 10px;
    }

    .search-products__item{
        gap: 10px;
    }

    .search-products__item-image{
        width: 40px;
        height: 40px;
    }

    .search-products__item-current-price{
        font-size: 14px;
    }

    .search-products__button{
        margin-right: 20px;
        font-size: 12px;
    }
}

@media(max-width: 1315px) {
    .header{
        .search-products__item{
            display: grid;
            grid-template-columns: 1fr 80px;
            grid-gap: 20px;
        }

        .search-products__item-image{
            grid-column: span 2;
            width: 50px;
            height: 50px;
        }

        .search-products__item-title{
            max-width: 100%;
        }
    }
}

@media(max-width: 1200px) {
    .header{
        padding: 20px 0;

        &__inner{
            padding-bottom: 55px;
            position: relative;
        }

        &__container{
            border-radius: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.10);
            background: #2C015C;

            padding: 12px 20px;
        }

        &__contacts{
            display: none;
        }

        &__phone{
            display: flex;
        }

        &__cataloge-button{
            position: absolute;
            max-width: 100%;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }

        &__wrapper-top{
            margin-bottom: 10px;
        }

        &__cart-title{
            font-size: 18px;
        }

        &__logo{
            max-width: 240px;
        }

        &.-js-fixed &__contacts{
            display: flex;
        }

        &.-js-fixed &__logo{
            display: none;
        }

        &.-js-fixed &__cataloge-button{
            max-width: 180px;
        }

        &.-js-fixed &__menu-button{
            max-width: 110px;
        }

        &.-js-fixed &__cart{
            margin-left: 0;
            max-width: fit-content;
        }

        &.-js-fixed &__cart-text span:nth-child(1) {
            display: none;
        }

        &.-js-fixed &__wrapper{
            max-width: 100%;
        }
    }
}

@media(max-width: 1024px) {
    .header{
        &__location{
            display: none;
        }

        &__container{
            gap: 30px;
        }

        &__logo{
            max-width: 180px;
        }

        &__cart{
            max-width: 130px;
        }

        &__cart-icon{
            width: 40px;
            height: 40px;
        }

        &__cart-title{
            font-size: 16px;
        }

        &__cart-text span:nth-child(1) {
            display: none;
        }

        &.-js-fixed &__menu-button{
            max-width: 40px;
        }
        
        &.-js-fixed &__menu-button-text{
            display: none;
        }

        &__nav-item:last-child {
            display: block;
        }

        &__nav-item:last-child &__location{
            display: flex;
            justify-content: center;
        }

        &__location-text{
            color: #160052;
        }
    }
}

@media(max-width: 992px) {

    .header{
        &.-js-fixed &__search-results{
            padding: 10px 0 10px 10px;
        }

        &.-js-fixed .search-products {
            &__items{
                padding-right: 10px;    
            }
            
            &__item{
                display: grid;
                grid-template-columns: 1fr;
                gap: 10px;
            }

            &__item-title{
                max-width: 100%;
            }

            &__button{
                margin-top: 15px;
                font-size: 12px;
                margin-right: 10px;
            }

            &__item-current-price{
                font-size: 14px;
            }

            &__item-image{
                grid-column: span 1;
            }
        }
    }
    
}

@media(max-width: 768px) {
    .header{
        &__container{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 0;
            grid-gap: 20px 45px;
            align-items: flex-end;
        }

        &__wrapper{
            order: -1;
            grid-column: span 2;
            max-width: 100%;
            padding-bottom: 13px;
            border-bottom: 1px dashed rgba(256, 256, 256, 0.1);
        }

        &__search-button{
            display: flex;
        }



        &__wrapper-top{
            margin-bottom: 0;
            gap: 20px;
        }

        &__logo{
            max-width: 160px;
        }

        &__cart{
            max-width: 100%;
            margin-left: auto;
            justify-content: flex-end;
            gap: 12px;
        }

        &.-js-fixed{
            padding: 15px 0;
        }

        &.-js-fixed &__container{
            display: flex;
            align-items: center;

            border-bottom: none;
        }

        &.-js-fixed &__wrapper{
            padding-bottom: 0;
            border-bottom: none;

            display: flex;
            align-items: center;
            gap: 20px;
        }

        &.-js-fixed &__contacts{
            display: none;
        }

        &.-js-fixed &__cart-title{
            display: none;
        }

        &.-js-fixed &__wrapper-top{
            display: flex;
        }

        &.-js-fixed &__search-button{
            display: flex;
        }

        &.-js-fixed &__menu-button{
            display: none;
        }

        &.-js-fixed &__wrapper-bottom{
            order: -1;
            max-width: 180px;
            width: 100%;
        }

        &.-js-fixed &__cataloge-button{
            max-width: 100%;
        }

        &.-js-fixed &__nav-button-text,
        &.-js-fixed &__phone-text,
        &.-js-fixed &__search-button-text {
            display: none;
        }

        &.-js-fixed &__cart-icon{
            width: 30px;
            height: 30px;
        }


        &__search{
            position: fixed;
            left: 0;
            top: 0;

            width: 100%;
            height: 100%;

            display: flex;
            align-items: center;
            flex-direction: column;

            background: rgba(22, 0, 82, 0.8);

            padding: 0 15px;

            opacity: 0;
            visibility: hidden;
            transition: opacity .4s ease;
        }
        
        &__search.visible{
            z-index: 1001;
            opacity: 1;
            visibility: visible;
            transition: opacity .4s ease;
        }

        &__search-form{
            margin-top: 36px;
        }

        &__search-close{
            display: flex;

            background: transparent;
            border: none;
            outline: none;
            cursor: pointer;
            padding: 0;
            margin: 0;

            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 16px;
        }

        .search-form__input input{
            padding-left: 41px;
        }

        .search-form__submit{
            right: auto;
            left: 0;
        }

        .search-products{
            position: static;
            margin-top: 15px;
            width: 100%;
        }
    }
}

@media(max-width: 475px) {
    .header{
        &__cart-title{
            font-size: 12px;
            margin-bottom: 3px;
        }

        &__cart-text{
            font-size: 12px;
        }

        &__logo{
            max-width: 140px;
        }

        &.-js-fixed &__cataloge-button-text span:nth-child(2) {
            display: none;
        }

        &.-js-fixed &__wrapper-bottom{
            max-width: 130px;
        }

        &.-js-fixed &__wrapper{
            gap: 15px;
            margin-right: 15px;
        }
    }
}