.select{
    position: relative;

    &__trigger{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;

        height: 50px;
        padding: 0 35px;

        border-radius: 40px;
        border: 1px solid rgba(30, 0, 112, 0.10);
        background: #fff;

        transition: opacity .4s ease;
        cursor: pointer;
    }



    &__trigger:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__trigger-icon{
        display: flex;
        align-items: center;
        justify-content: center;

        transition: transform .4s ease;
    }

    &__trigger-text{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 1; /* 200% */
    }

    &__content{
        border-radius: 15px;
        background: #FFF;
        box-shadow: 0px 14px 40px 0px rgba(0, 0, 0, 0.10);
        padding: 20px 30px;

        width: 100%;

        opacity: 0;
        overflow: hidden;
        transition: opacity .4s ease;

        position: absolute;
        top: 120%;
        left: 0;

        z-index: -1;
    }

    &__content-item{
        list-style: none;

        color:  #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 1.2; /* 200% */

        transition: color .4s ease;
        cursor: pointer;
        margin-bottom: 15px;
    }

    &__content-item:last-child{
        margin-bottom: 0;
    }

    &__content-item:hover{
        color: #F7AEFF;
        transition: color .4s ease;
    }

    &.-js-select-open &__content{
        opacity: 1;
        overflow: visible;
        transition: opacity .4s ease;
        z-index: 2;
    }

    &.-js-select-open &__trigger-icon{
        transform: rotate(180deg);
        transition: transform .4s ease;
    }
}

@media(max-width: 768px) {
    .select{
        &__trigger{
            padding: 0 20px;
            height: 45px;
        }

        &__trigger-text{
            font-size: 14px;
        }

        &__content{
            padding: 20px;
        }

        &__content-item{
            font-size: 14px;
        }
    }
}