.products{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
}

@media(max-width: 1200px) {
    .products{
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 15px;
    }
}

@media(max-width: 768px) {
    .products{
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
    }
}