.mobile-menu{
    &__nav{
        position: fixed;
        top: 0;
        left: 0;

        background: rgba(22, 0, 82, 0.8);

        width: 100%;
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: flex-start;

        opacity: 0;
        visibility: hidden;
        transition: opacity .4s ease;

    }

    &__nav.visible{
        opacity: 1;
        visibility: visible;
        transition: opacity .4s ease;
        z-index: 1000;
    }
    
    &__nav-list{
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        
        background: #fff;
        border-radius: 15px;
        width: 100%;

        height: fit-content;
        overflow-y: auto;

        padding: 6px 30px 10px;

        margin: 65px 15px 0 15px;
    }

    &__nav-item{
        padding: 20px 0;
        border-bottom: 1px dashed rgba(30, 0, 112, 0.1);

        width: 100%;

        display: flex;
        justify-content: center;
    }

    &__nav-item:nth-child(4) {
        border-bottom: none;
    }

    &__nav-item:last-child{
        display: none;
        padding-bottom: 0;
        border-bottom: none;
    }

    &__nav-link{
        color: #160052;
        font-family: Days One;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-decoration: none;
    }

    &__location{
        display: flex;
        align-items: center;
        gap: 11px;
    }

    &__location-text{
        color: #160052;
        font-variant-numeric: lining-nums proportional-nums;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 800;
        line-height: normal;
    }
}

@media(max-width: 1024px) {
    .mobile-menu{
        &__nav-item:nth-child(4) {
            border-bottom: 1px dashed rgba(30, 0, 112, 0.1);
        }

        &__nav-item:last-child{
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &__nav-list{
            padding-bottom: 20px;
        }
    }
}