.breadcrumbs{
    margin: 25px 0 25px;

    &__list{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 10px 0;
    }

    &__list-item{
        position: relative;
        list-style: none;
        padding-right: 14px;
        margin-right: 15px;
    }

    &__list-item::after{
        content: '/';
        position: absolute;

        right: -1px;
        top: 50%;
        transform: translateY(-50%);

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        line-height: 32px; /* 266.667% */
    }

    &__list-item:last-child{
        margin: 0;
        padding: 0;
    }

    &__list-item:last-child::after{
        display: none;
    }

    &__list-item:last-child &__list-link{
        font-weight: 600;
    }

    &__list-item:last-child &__list-link::before{
        display: none;
    }

    &__list-link{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.2; /* 266.667% */

        text-decoration: none;

        position: relative;
        padding-bottom: 4px;

        transition: opacity .4s ease;
    }

    &__list-link:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__list-link::before{
        content: '';
        position: absolute;

        left: 50%;
        bottom: 0;
        transform: translateX(-50%);

        width: 100%;
        height: 1px;

        background: rgb(22, 0, 82, 0.1);
    }
}