.about-company{
    margin-bottom: 130px;

    &__wrapper{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 50px;
    }

    &__content{
        max-width: 940px;
        width: 100%;
    }

    &__aside{
        max-width: 340px;
        width: 100%;
        position: sticky;
        top: 50px;
        
        border-radius: 20px;
        border: 3px solid #FBFB00;
        background: #FFF;

        padding: 30px 40px 40px;
    }

    &__aside li {
        padding: 25px 0;
        list-style: none;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.1);
    }

    &__aside li:first-child{
        padding-top: 0;
    }

    &__aside li:last-child{
        padding-bottom: 0;
        border-bottom: none;
    }

    &__aside a{
        color:#160052;
        font-family: Nunito, sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 120% */
        text-decoration: none;
        transition: color .4s ease;
    }

    &__aside a:hover{
        color: #F7AEFF;
        transition: color .4s ease;
    }

    &__main-image{
        width: 100%;
        height: 500px;
        margin-bottom: 40px;
    }

    &__main-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
    }

    &__content p,
    &__content li{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }

    &__content li{
        position: relative;
        list-style: none;
        margin-bottom: 16px;
        padding-left: 21px;
    }

    &__content li::before{
        content: '';
        position: absolute;

        top: 0;
        left: 0;
    }

    &__content ul li::before{
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #F27FFF;

        top: 8px;
    }

    &__content ol li::before{
        counter-increment: section;
        content: counter(section)'.';

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }

    &__content li:last-child{
        margin-bottom: 0;
    }

    &__content ul,
    &__content ol{
        padding-left: 21px;
    }

    &__content blockquote{
        padding: 40px 15px 40px 40px;
        border-radius: 20px;
        border: 3px solid #FBFB00;
        background: #FFF;

        margin-top: 50px;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 21px;
        font-style: italic;
        font-weight: 800;
        line-height: 30px; /* 142.857% */

        position: relative;
        overflow: hidden;
    }

    &__content blockquote::before{
        content: url('../../../img/icons/blockquote-icon.svg');
        position: absolute;
        top: 0;
        left: 40px;
    }

    &__content h3{
        color: #160052;
        font-family: Days One;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 20px;
    }
    
    &__block{
        padding-bottom: 50px;
        margin-bottom: 50px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &__block:nth-child(4) {
        padding-bottom: 0;
        border-bottom: none;
        margin-bottom: 77px;
    }

    &__block:last-child{
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
    }
    
    &__gallery-items{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 20px;
    }

    &__gallery-item{
        width: 100%;
        height: 100%;

        position: relative;
        cursor: pointer;
    }

    &__gallery-item-decor{
        position: absolute;

        width: 100%;
        height: 100%;

        left: 0;
        top: 0;
        z-index: 1;

        background: rgba(251, 251, 0, 0.6);
        border-radius: 20px;
        
        opacity: 0;
        visibility: hidden;
        transition: all .8s ease;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__gallery-item:hover &__gallery-item-decor{
        opacity: 1;
        visibility: visible;
        transition: all .8s ease;
    }
    

    &__gallery-item-image{
        display: block;
        width: 100%;
        height: 100%;
    }

    &__gallery-item-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
    }

    &__content table{
        border-collapse: collapse;
        width: 100%;
    }

    &__content th{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 800;
        line-height: 24px; /* 150% */
        text-align: left;
    }

    &__content th:nth-child(1){
        padding: 0 25px;
        border-radius: 20px 0 0 20px;

    }

    &__content th:nth-child(2) {
        padding: 0 40px;
        border-left: 1px solid rgba(22, 0, 82, 0.1);
        border-radius: 0 20px 20px 0;

    }

    &__content td:nth-child(1) {
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 150% */
        padding: 0 25px;

        border-radius: 20px 0 0 20px;

    }

    &__content td:nth-child(2) {
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 800;
        line-height: 24px; /* 150% */

        padding: 0 40px;

        border-left: 1px solid #ECECEC;

        border-radius: 0 20px 20px 0;

    }

    &__content tr{
        height: 60px;
    }
    
    &__content tr:nth-child(odd) {
        background: #F3F3F3;
    }

    &__content tr:first-child{
        background: #FBFB00;
    }
}

@media(max-width: 1440px) {
    .about-company{
        &__wrapper{
            gap: 30px;
        }

        &__aside{
            padding: 30px;
            max-width: 300px;
        }

        &__aside li{
            padding: 20px 0;
        }

        &__aside a{
            font-size: 18px;
        }
    }
}

@media(max-width: 1200px) {
    .about-company{
        margin-bottom: 100px;

        &__block{
            margin-bottom: 40px;
            padding-bottom: 40px;
        }

        &__block:nth-child(4) {
            margin-bottom: 40px;
        }

        &__gallery-items{
            grid-gap: 15px;
        }

        &__content h3{
            font-size: 22px;
        }

        &__content ul,
        &__content ol{
            padding-left: 14px;
        }

        &__content blockquote{
            font-size: 18px;
            margin-top: 40px;
        }

        &__content blockquote::before{
            top: -5px;
        }

        &__main-image{
            height: 460px;
            margin-bottom: 30px;
        }

        &__aside{
            max-width: 260px;
        }

        &__gallery-item{
            height: 210px;
        }

        &__content th:nth-child(1),
        &__content td:nth-child(1) {
            padding: 0 16px;
        }

        &__content th:nth-child(2),
        &__content td:nth-child(2) {
            padding: 0 16px 0 20px;
        }
        
        &__content th{
            font-size: 14px;
            line-height: 20px;
        }

        &__content td:nth-child(1),
        &__content td:nth-child(2){
            font-size: 14px;
            line-height: 20px;
        }
    }
}

@media(max-width: 1024px) {
    .about-company{
        &__aside{
            display: none;
        }

        &__content{
            max-width: 100%;
        }

        &__main-image{
            height: 400px;
            margin-bottom: 20px;
        }

        &__block{
            margin-bottom: 30px;
            padding-bottom: 30px;
        }

        &__block:nth-child(4) {
            margin-bottom: 30px;
        }

        &__content p,
        &__content li{
            font-size: 14px;
        }

        &__content li{
            margin-bottom: 12px;
            padding-left: 16px;
        }

        &__content h3{
            font-size: 20px;
            margin-bottom: 15px;
        }

        &__content ul,
        &__content ol{
            padding-left: 0;
        }

        &__content blockquote{
            font-size: 16px;
            line-height: 26px;
            margin-top: 30px;
        }

        &__gallery-items{
            grid-gap: 10px;
        }

        &__gallery-item{
            height: 180px;
        }
    }
}

@media(max-width: 768px) {
    .about-company{
        margin-bottom: 80px;

        &__main-image{
            height: 340px;
        }

        &__content h3{
            font-size: 18px;
        }

        &__content blockquote{
            padding: 30px 15px 30px 30px;
        }

        &__content blockquote::before{
            top: -10px;
            left: 30px;
        }

        &__gallery-item{
            height: 160px;
        }

        &__gallery-item-image img{
            border-radius: 15px;
        }

        &__content th:nth-child(1),
        &__content td:nth-child(1){
            width: 78%;
            padding: 0 12px;

            font-size: 12px;
            line-height: 18px;
        }

        &__content th:nth-child(2),
        &__content td:nth-child(2) {
            text-align: center;
            padding: 0 12px;

            font-size: 12px;
            line-height: 18px;
        }
    }
}

@media(max-width: 475px) {
    .about-company{
        margin-bottom: 60px;

        &__content th:nth-child(1),
        &__content td:nth-child(1) {
            width: 70%;
        }

        &__gallery-item{
            height: 140px;
        }

        &__content tr{
            height: 70px;
        }

        &__content blockquote{
            padding-left: 20px;
            padding-bottom: 20px;
        }

        &__main-image{
            height: 300px;
        }
    }
}

@media(max-width: 425px) {
    .about-company{
        &__gallery-items{
            grid-template-columns: repeat(2, 1fr);
        }
    }
}