.order{
    margin-bottom: 130px;

    &__form-inner{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 50px;
    }

    &__content{
        max-width: 820px;
        width: 100%;
    }

    &__aside{
        max-width: 460px;
        width: 100%;

        position: sticky;
        top: 120px;
    }

    &__title{
        margin-bottom: 40px;
    }
}

.order-block{
    position: relative;
    padding: 40px 0 40px 50px;
    border-bottom: 1px dashed rgba(22, 0, 82, 0.2);

    &:first-child{
        border-top: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &::before{
        counter-increment: section;
        content: counter(section);

        position: absolute;

        top: 35px;
        left: 0;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 150% */

        width: 34px;
        height: 34px;
        border-radius: 50%;

        background: #FBFB00;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 120% */
        margin-bottom: 26px;
    }

    &__inputs{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
    }

    &__input-wrap{
        width: 100%;
    }

    &__input {
        width: 100%;
    }

    &__input input{
        width: 100%;
        height: 50px;

        border-radius: 15px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        background: #F7F6FA;

        outline: none;
        border: none;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        padding: 0 20px;
    }

    &__input-label{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;

        margin-bottom: 7px;
    }

    &__radios{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
    }

    &__radio{
        height: 164px;
    }

    &__radio label{
        display: block;
        border-radius: 15px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        background: #F7F6FA;

        padding: 20px;
        transition: background .4s ease;
        cursor: pointer;

        height: 100%;
    }

    

    &__radio label:hover {
        background: #F4F0F9;
        transition: background .4s ease;
    }

    &__radio-input{
        display: none;
    }

    &__radio-input:checked + label &__radio-icon::before{
        display: block;
    }

    &__radio-input:disabled + label {
        opacity: .7;
    }

    &__radio-input:disabled + label:hover{
        background: #F7F6FA;
    }

    &__radio-input:checked + label{
        background: #F4F0F9;
    }

    &__radio-input:checked + label &__radio-icon{
        background: #FBFB00;
    }

    &__radio-icon{
        display: block;

        width: 30px;
        height: 30px;

        border-radius: 50%;
        background: #FFF;

        border: 1px solid #E4E3E9;

        margin-bottom: 15px;

        position: relative;
    }

    &__radio-icon::before{
        content: url('../img/icons/tick.svg');
        position: absolute;

        top: 50%;
        left: 50%;

        transform: translate(-50%, -50%);

        display: none;
    }

    &__radio-title{
        display: block;
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px; /* 112.5% */

        margin-bottom: 5px;
    }

    &__radio-text{
        display: block;
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px; /* 150% */
    }

    &__input textarea{
        width: 100%;
        min-height: 150px;

        resize: none;
        border: none;
        margin: 0;
        outline: none;

        border-radius: 15px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        background: #F7F6FA;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        padding: 20px;
    }
}

.order-tabs{
    &__radios{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
    }

    &__radio label{
        display: flex;
        align-items: flex-start;
        gap: 15px;
        border-radius: 15px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        padding: 20px;
        background: #F7F6FA;
        cursor: pointer;
    }

    &__radio-title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 150% */

        display: block;
        margin-bottom: 5px;
    }
    &__radio-text{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px; /* 150% */

        display: block;
        margin-bottom: 10px;
    }

    &__radio-price{
        font-family: Days One;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__radio:nth-child(1) &__radio-price{
        color: #F27FFF;
    }

    &__radio:nth-child(2) &__radio-price{
        color: #41D061;
    }

    &__radio-icon{
        display: block;

        width: 30px;
        height: 30px;
        background: #fff;

        border-radius: 50%;
        border: 1px solid #E4E3E9;

        position: relative;
    }

    &__radio-icon::after{
        content: url('../img/icons/tick.svg');
        position: absolute;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        display: none;
    }

    &__radio > input{
        display: none;
    }

    &__radio > input:checked + label{
        background: #F4F0F9;
    }

    &__radio > input:checked + label &__radio-icon::after{
        display: block;
    }

    &__radio > input:checked + label &__radio-icon{
        background: #FBFB00;
    }

    &__checkbox{
        margin-bottom: 30px;
    }

    &__checkbox > input{
        display: none;
    }

    &__checkbox > input:checked + label &__checkbox-icon::before{
        display: block;
    }

    &__checkbox > input:checked + label &__checkbox-icon{
        background: #FBFB00;
    }

    &__checkbox label{
        display: flex;
        align-items: center;
        gap: 13px;

        cursor: pointer;
    }

    &__checkbox-icon{
        display: block;

        width: 24px;
        height: 24px;

        border-radius: 6px;
        border-bottom: 2px solid rgba(228, 227, 233, 0.40);
        
        background: #F7F6FA;

        position: relative;
    }

    &__checkbox-icon::before{
        content: url('../img/icons/tick.svg');
        position: absolute;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        display: none;
    }

    &__checkbox-title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 171.429% */
    }

    &__fields-block:nth-child(1) {
        grid-template-columns: 1fr;
    }

    &__fields-block.-js-visible{
        display: grid;
    }

    &__fields-block{
        display: none;
        grid-gap: 20px;
    }

    &__fields-block:nth-child(1) .order-block__input-wrap:nth-child(2) {
        max-width: 180px;
    }

    &__fields-block:nth-child(2) {
        grid-template-columns: repeat(4, 1fr);
    }

    &__fields-block:nth-child(2) .order-block__input-wrap:nth-child(1) {
        grid-column: span 4;
    }

    &__fields-block:nth-child(2) .order-block__input-wrap:last-child {
        grid-column: span 4;
    }

    &__content-item{
        display: none;
    }

    &__content-item.-js-active{
        display: block;
        margin-top: 30px;
    }

    &__map{
        width: 100%;
        height: 500px;
    }


}

.order-aside{
    border-radius: 20px;
    border: 3px solid #FBFB00;
    background: #FFF;

    flex-shrink: 0;

    padding: 50px 50px 40px 50px;

    &__title{
        color: #160052;
        text-align: center;
        font-family: Days One;
        font-size: 30px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 30px;
    }

    &__block{
        padding: 20px 0;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.1);
    }

    &__block:first-child{
        border-top: 1px dashed rgba(22, 0, 82, 0.1);
    }

    &__wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;

        margin-bottom: 12px;
        gap: 20px;
    }

    &__wrapper:last-child{
        margin-bottom: 0;
    }

    &__product-title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 150% */

        max-width: 240px;
    }

    &__new-price{
        color:#160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        flex-shrink: 0;
    }

    &__old-price{
        color: #160052;
        font-family: Days One;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        position: relative;
        flex-shrink: 0;
    }

    &__old-price::after{
        content: '';
        position: absolute;

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        width: 110%;
        height: 1px;

        background: #F27FFF;
    }

    &__product-amount{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }

    &__container{
        margin-bottom: 30px;
    }

    &__block:nth-child(3) &__wrapper{
        margin-bottom: 5px;
    }

    &__block:nth-child(3) &__wrapper:last-child &__wrapper-amount{
        color: #41D061;
    }

    &__wrapper-title{
        color:#160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 150% */
    }

    &__wrapper-amount{
        color:#160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 800;
        line-height: 24px; /* 150% */
    }
    
    &__total{
        display: flex;
        align-items: center;    
        justify-content: space-between;
        gap: 30px;

        margin-bottom: 30px;
    }

    &__total-label{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 100% */
    }

    &__total-price{
        color: #160052;
        font-family: Days One;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__submit{
        border-radius: 60px;
        background: #FBFB00;

        width: 100%;
        height: 70px;

        display: flex;
        align-items: center;
        justify-content: center;

        gap: 15px;

        cursor: pointer;
        outline: none;
        border: none;

        padding: 0 15px;

        margin-bottom: 20px;
    }

    &__submit-text{
        color: #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;
    }

    &__submit-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__warning{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__warning-text{
        opacity: .7;
        color: #160052;
        text-align: center;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px; /* 150% */
        max-width: 300px;
    }

    &__warning-text a{
        position: relative;
        font-weight: 700;
        text-decoration: none;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px;
    }

    &__warning-text a::before{
        content: '';
        position: absolute;

        left: 50%;
        transform: translateX(-50%);
        bottom: -2px;

        width: 100%;
        height: 1px;

        background: rgba(22, 0, 82, 0.2);
    }
}


@media(max-width: 1440px) {
    .order{
        &__title{
            margin-bottom: 30px;
        }
    }

    .order-block{
        padding: 30px 0 30px 50px;

        &::before{
            top: 25px;
        }

        &__title{
            margin-bottom: 15px;
        }

        &__radios{
            grid-gap: 15px;
        }

        &__inputs{
            grid-gap: 15px;
        }
    }

    .order-tabs{
        &__radios{
            grid-gap: 15px;
        }

        &__fields-block{
            grid-gap: 15px;
        }

        &__map{
            height: 440px;
        }
    }

    .order-aside{
        padding: 40px;
        max-width: 440px;

        &__title{
            font-size: 28px;
            margin-bottom: 20px;
        }

        &__submit{
            height: 60px;
        }

        &__submit-text{
            font-size: 16px;
        }
    }
}

@media(max-width: 1200px) {
    .order{
        margin-bottom: 100px;

        &__form-inner{
            gap: 30px;
        }
    }

    .order-block{
        &__radios{
            grid-template-columns: repeat(2, 1fr);
        }

        &__radio{
            height: auto;
        }
        
    }

    .order-aside{
        padding: 30px;
        max-width: 400px;

        &__title{
            font-size: 26px;
        }

        &__total-price{
            font-size: 20px;
        }

        &__total-label{
            font-size: 20px;
        }
    }
}

@media(max-width: 1024px) {
    .order{
        &__form-inner{
            flex-direction: column;
            align-items: flex-start;
            gap: 50px;
        }

        &__content{
            max-width: 100%;
        }
    }

    .order-tabs{
        &__radios{
            grid-gap: 10px;
        }

        &__fields-block{
            grid-gap: 10px;
        }
    }

    .order-block{
        &__radios{
            grid-template-columns: repeat(4, 1fr);
            grid-gap: 10px;
        }
        
        &__inputs{
            grid-gap: 10px;
        }

        &__input-label{
            margin-bottom: 4px;
        }
    }

    .order-aside{
        order: -1;
        max-width: 100%;

        position: static;

        &__product-title{
            max-width: 100%;
        }
    }
}

@media(max-width: 768px) {
    .order{
        margin-bottom: 80px;

        &__form-inner{
            gap: 30px;
        }

        &__title{
            margin-bottom: 25px;
        }
    }

    .order-aside{
        padding: 20px;

        &__title{
            font-size: 22px;
            margin-bottom: 15px;
        }

        &__block{
            padding: 15px 0;
        }

        &__container{
            margin-bottom: 20px;
        }

        &__total{
            margin-bottom: 20px;
        }

        &__submit{
            height: 50px;
            margin-bottom: 15px;
        }

        &__total-price{
            font-size: 18px;
        }

        &__total-label{
            font-size: 18px;
        }
    }

    .order-block{

        padding: 20px 0 20px 40px;
        position: relative;
        &::before{
            width: 28px;
            height: 28px;

            font-size: 14px;

            top: 18px;
        }

        &__radio-title{
            font-size: 14px;
        }

        &__radio label{
            padding: 15px;
        }

        &__title{
            font-size: 18px;
        }

        &__radio-icon{
            margin-bottom: 10px;
        }

        &__radios{
            grid-template-columns: repeat(2, 1fr);
        }

        &__map{
            position: absolute;
            width: 100%;
            bottom: 0;
            left: 0;
        }
    }

    .order-tabs{
        &__radio label{
            padding: 15px;
            gap: 10px;
            flex-direction: column;
        }

        &__radio-icon{
            width: 26px;
            height: 26px;
        }

        &__radio-icon::before{
            top: 45%;
        }

        &__radio-title{
            font-size: 14px;
            line-height: 20px;
        }

        &__radio-text{
            margin-bottom: 5px;
        }

        &__radio-price{
            font-size: 14px;
            line-height: 20px;
        }
    }
}

@media(max-width: 475px) {
    .order{
        margin-bottom: 60px;
    }

    .order-block{
        padding: 20px 15px;

        &::before{
            display: none;
        }

        &__inputs{
            grid-template-columns: 1fr;
        }

        &__input-label{
            font-size: 12px;
        }


    }

    .order-tabs{
        &__radios{
            grid-template-columns: 1fr;
        }

        &__fields-block:nth-child(2) {
            grid-template-columns: 1fr;
        }

        &__fields-block:nth-child(1) .order-block__input-wrap:nth-child(2){
            max-width: 100%;
        }

        &__fields-block:nth-child(2) .order-block__input-wrap:nth-child(1){
            grid-column: span 1;
        }

        &__fields-block:nth-child(2) .order-block__input-wrap:last-child{
            grid-column: span 1;
        }

        &__map{
            height: 600px;
        }
    }

    .order-aside{
        padding: 20px 15px;

        &__title{
            font-size: 20px;
        }

        &__product-title{
            font-size: 14px;
            line-height: 20px;
        }

        &__new-price{
            font-size: 14px;
        }

        &__wrapper-title{
            font-size: 14px;
        }

        &__wrapper-amount{
            font-size: 14px;
        }

        &__total-label{
            font-size: 16px;
        }

        &__total-price{
            font-size: 16px;
        }

        &__submit-text{
            font-size: 14px;
        }
    }
}