.product{
    border-radius: 20px;
    border: 3px solid #FBFB00;
    background: #FFF;

    height: 460px;

    position: relative;
    overflow: hidden;

    &::before{
        content: url('../img/decor/product-yellow-bottom.svg');
        position: absolute;
        bottom: -8px;
        right: 0;
    }

    &--sale{
        border-color: #F27FFF;
    }

    &--sale::before{
        content: url('../img/decor/product-purple-bottom.svg');
    }

    &--sale::after{
        content: url('../img/decor/product-purple-top.svg');
        position: absolute;
        top: -3px;
        left: 0;
    }

    &__form{
        height: 100%;
    }

    &__form-inner{
        display: flex;
        flex-direction: column;

        width: 100%;
        padding: 37px 27px 21px;

        position: relative;
        height: 100%;

        box-sizing: border-box;
    }

    &--sale &__form-inner{
        padding-bottom: 15px;
    }

    &__label{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;

        color:  #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;

        max-width: 225px;
        width: 100%;
        padding: 12px 22px;
    }

    &__image{
        display: block;
        height: 260px;
        max-width: 260px;
        width: 100%;
        margin-bottom: 20px;
        transition: transform .4s ease;

        margin: 0 auto 20px;
    }

    &__image:hover{
        transform: scale(1.1);
        transition: transform .4s ease;
    }

    &__image img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &__title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 150% */

        margin-bottom: 30px;
        margin-top: auto;
    }

    &--sale &__title{
        margin-bottom: 15px;
    }

    &__price-wrapper{
        margin-top: auto;

        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__current-price{
        color: #160052;
        font-family: Days One;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        display: inline-block;
    }

    &__old-price{
        color: #160052;
        font-family: Days One;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-top: 3px;
        position: relative;

        display: inline-block;
    }

    &__old-price::after{
        content: '';
        position: absolute;

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        width: 110%;
        height: 1px;
        background: #F27FFF;
    }

    &__buy{
        position: absolute;
        bottom: -2px;
        right: -2px;

        display: flex;
        align-items: center;
        gap: 10px;

        padding: 22px 26px;
        z-index: 10;

        background: transparent;
        border: none;
        outline: none;
        cursor: pointer;
        transition: opacity .4s ease;
    }

    &__buy:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__buy-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__buy-text{
        color: #160052;
        font-family: Days One;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

@media(max-width: 1440px) {
    .product{

        &::before{
            right: -80px;
        }

        &__form-inner{
            padding: 37px 15px 20px;
        }

        &__buy-text{
            display: none;
        }
    }
}

@media(max-width: 768px) {
    .product{
        height: 420px;

        &__image{
            height: 220px;
        }
    }
}

@media(max-width: 576px) {
    .product{
        height: 380px;

        // &--sale &__title{
        //     margin-bottom: 10px;
        // }

        &__title{
            display: -webkit-box;
            -webkit-line-clamp: 2; // количество строк
            -webkit-box-orient: vertical;
            overflow: hidden;
            min-height: 36px;
        }

        &__current-price{
            font-size: 18px;
        }

        &__image{
            height: 180px;
        }

        &__form-inner{
            padding-top: 50px;
        }
    }
}


@media(max-width: 475px) {
    .product{

        height: 260px;

        &--sale::after{
            top: -22px;
            left: -30px;
        }

        &::before{
            bottom: -30px;
            right: -95px;
        }

        &__label{
            font-size: 12px;
            padding: 8px 16px;
        }

        &__image{
            width: 110px;
            height: 110px;
        }

        &__title{
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 20px;
        }

        &--sale &__title{
            margin-bottom: 3px;
        }

        &__current-price{
            font-size: 14px;
        }

        &__old-price{
            font-size: 12px;
            margin-top: 0;
        }

        &__form-inner{
            padding-top: 25px;
            padding-bottom: 15px;
        }

        &__buy{
            padding: 12px 14px;
        }
    }
}