.success-modal{
    &__inner{
        max-width: 460px;
        width: 100%;
        background-image: url('../img/decor/success-modal-bg.svg');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        background-color: #41D061;
    }

    .modal__title{
        max-width: 220px;
        color: #fff;
        margin-bottom: 8px;
    }

    .modal__subtitle{
        text-align: center;
        color: #fff;
        margin-bottom: 0;
    }
}

@media(max-width: 1200px) {
    .success-modal{
        .modal__image{
            max-width: 200px;
        }

        &__inner{
            max-width: 380px;
        }
    }
}

@media(max-width: 1024px) {
    .success-modal{
        &__inner{
            max-width: 360px;
        }

        .modal__image{
            max-width: 180px;
        }
    }
}

@media(max-width: 768px) {
    .success-modal{
        .modal__image{
            max-width: 160px;
        }

        &__inner{
            max-width: 340px;
        }
    }
}

@media(max-width: 475px) {
    .success-modal{
        &__inner{
            max-width: 300px;
        }

        .modal__image{
            max-width: 140px;
        }

        .modal__subtitle{
            max-width: 260px;
        }
    }
}