.pagination{
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 20px;

    padding: 20px 30px;
    background: #fff;
    border: 3px solid var(--fbfb-00, #FBFB00);
    border-radius: 20px;

    &__button{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 12px;

        outline: none;
        cursor: pointer;
        background: transparent;
        margin: 0;
        padding: 0;
        border: none;

        transition: opacity .4s ease;
    }

    &__button:hover{
        opacity: .7;
        transition: opacity .4s ease
        ;
    }

    &__button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__button-text{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px;
    }

    &__list{
        display: flex;
        align-items: center;
        gap: 40px;
    }

    &__item{
        list-style: none;
    }

    &__link{
        width: 30px;
        height: 30px;

        display: flex;
        align-items: center;
        justify-content: center;

        border-radius: 50%;

        text-decoration: none;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 1; /* 150% */

        transition: background .4s ease;
    }

    &__link:hover{
        background: #FBFB00;

        transition: background .4s ease;
    }

    &__link.disabled{
        cursor: auto;
    }

    &__link.disabled:hover{
        background: transparent;

    }
}

@media(max-width: 1023px) {
    .pagination{
        &__link{
            width: auto;
            height: auto;
        }

        &__link:hover{
            background: transparent;
        }
    }
}

@media(max-width: 768px) {
    .pagination{
        padding: 15px 20px;
        margin-top: 10px;

        &__list{
            gap: 30px;
        }

        &__button-text{
            display: none;
        }

        &__link{
            font-size: 14px;
        }
    }
}

@media(max-width: 475px) {
    .pagination{
        &__list{
            gap: 25px;
        }
    }
}

@media(max-width: 375px) {
    .pagination{
        &__list{
            gap: 20px;
        }
    }
}