.ui-button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    border-radius: 40px;
    border: 2px solid #F27FFF;
    background: #fff;
    outline: none;
    cursor: pointer;
    transition: opacity .4s ease;

    color: #160052;
    font-family: Days One;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }
}