.cataloge-page{
    margin-bottom: 130px;

    &__title{
        margin-bottom: 30px;
    }
    
    &__filters{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 20px;

        margin-bottom: 50px;
    }

    .select:last-child{
        margin-left: auto;
    }

    .select:last-child .select__trigger{
        padding-left: 167px;
        position: relative;
    }

    .select:last-child .select__trigger::before{
        content: 'Сортировать по:';
        position: absolute;

        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 1; /* 200% */

        top: 48%;
        transform: translateY(-50%);
        left: 35px;
    }
}


@media(max-width: 1200px) {
    .cataloge-page{
        margin-bottom: 100px;

        &__filters{
            margin-bottom: 40px;
        }

    }
}

@media(max-width: 1024px) {
    .cataloge-page{
        margin-bottom: 80px;
        
        &__filters{
            margin-bottom: 30px;
            gap: 15px;
        }

        .select:last-child{
            margin-left: 0;
        }
    }
}

@media(max-width: 768px) {
    .cataloge-page{

        &__filters{
            gap: 10px;
        }

        .select:last-child .select__trigger::before{
            left: 20px;
        }

        .select:last-child .select__trigger{
            padding-left: 155px;
        }
    }
}

@media(max-width: 475px) {
    .cataloge-page{
        margin-bottom: 60px;
    }
}

@media(max-width: 425px) {
    .cataloge-page{
        &__filters{
            flex-direction: column;
            flex-wrap: nowrap;
            align-items: flex-start;
        }

        .select{
            width: 100%;
        }

        .select:last-child .select__trigger{
            justify-content: flex-start;
            // padding-left: 210px;
        }

        .select:last-child .select__trigger::before{
            left: 35px;
            font-size: 14px;
        }
    }
}