.product-info{
    margin-top: 40px;
    margin-bottom: 130px;

    &__container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 50px;

        border-radius: 20px;
        border: 3px solid #FBFB00;
        background: #FFF;

        padding: 50px 25px 26px 40px;

        margin-bottom: 20px;

        overflow: hidden;

        position: relative;
    }

    &__content::after{
        content: url('../../../img/decor/product-info-yellow-bottom.svg');
        position: absolute;
        right: -28px;
        bottom: -8px;

        z-index: 1;
    }

    &__title{
        color: #160052;
        font-family: Days One;
        font-size: 40px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-bottom: 23px;
    }

    &__wrapper{
        display: flex;
        align-items: center;
        gap: 30px;
        padding-bottom: 23px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
        margin-bottom: 23px;
    }

    &__existence-text{
        position: relative;
        padding-left: 20px;

        color: #41D061;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 200% */
    }

    &__existence-text::before{
        content: url('../../../img/icons/green-tick.svg');
        position: absolute;

        top: 52%;
        transform: translateY(-50%);
        left: 0;
    }

    &__articul-text{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 200% */
    }

    &__text{
        margin-bottom: 23px;
        max-width: 630px;
    }

    &__text p{
        color:  rgba(22, 0, 82, 0.7);
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 171.429% */
    }
    
    &__characteristics{
        max-width: 445px;
        width: 100%;
        margin-bottom: 42px;
    }

    &__characteristics-item{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: 20px;

        list-style: none;

        margin-bottom: 15px;
    }

    &__characteristics-item:last-child{
        margin-bottom: 0;
    }

    &__characteristics-title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 1; /* 142.857% */
    }

    &__characteristics-decor{
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
        margin-bottom: 3px;
    }

    &__characteristics-value{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 800;
        line-height: 1; /* 142.857% */

        max-width: 190px;
        width: 100%;
        text-align: left;
    }

    &__price{
        display: flex;
        align-items: center;
        gap: 32px;
    }

    &__current-price{
        color: #160052;
        font-family: Days One;
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;
    }

    &__old-price{
        color: #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;

        position: relative;
    }

    &__old-price::after{
        content: '';
        position: absolute;

        height: 1px;
        width: 110%;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #F27FFF;
    }

    &__form-submit{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        max-width: 320px;
        width: 100%;

        height: 80px;

        cursor: pointer;
        outline: none;
        background: transparent;
        border: none;
        transition: opacity .4s ease;

        position: absolute;
        right: 0;
        bottom: 0;

        z-index: 2;
    }

    &__form-submit:hover{
        opacity: 0.7;
        transition: opacity .4s ease;
    }

    &__form-submit-icon {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__form-submit-text{
        color: #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__slider{
        display: flex;
        align-items: center;
        gap: 30px;
    }

    &__main-slider{
        max-width: 500px;
        width: 100%;
    }

    &__main-image{
        height: 500px;
        width: 100%;
    }

    &__main-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__thumbs-slider{
        max-width: 100px;
        width: 100%;
        height: 350px;
    }

    &__thumb{
        opacity: .5;
    }

    &__thumb.swiper-slide-active{
        opacity: 1;
    }

    &__thumb-image{
        width: 100%;
        height: 100px;
    }

    &__thumb-image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__thumbs-wrapper{
        position: relative;
        height: 420px;

        display: flex;
        align-items: center;
    }

    &__thumbs-button{
        position: absolute;

        cursor: pointer;
        outline: none;
        background: transparent;
        margin: 0;
        padding: 0;
        border: none;

        left: 50%;
        transform: translateX(-50%);
    }

    &__thumbs-button--prev{
        top: 0;
    }

    &__thumbs-button--next{
        bottom: 0;
    }

    &__tabs{
        padding: 32px 50px 50px;
        border-radius: 20px;
        border: 3px solid #FBFB00;
        background: #FFF;
    }

    &__tabs-buttons{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 20px 30px;

        padding-bottom: 28px;
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &__tabs-button{
        color: rgba(22, 0, 82, 0.50);
        font-family: Days One;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        cursor: pointer;
        padding: 0;
        margin: 0;
        outline: none;
        background: transparent;
        border: none;

        transition: color .4s ease;

        position: relative;
    }

    &__tabs-button::before{
        content: '';
        position: absolute;

        width: 100%;
        height: 3px;

        background: #F27FFF;

        bottom: -32px;
        left: 50%;
        transform: translateX(-50%);

        display: none;
    }

    &__tabs-button:hover{
        color: #F7AEFF;
        font-family: Days One;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__tabs-button.-js-active{
        color: #160052;
        font-family: Days One;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;
    }

    &__tabs-button.-js-active::before{
        display: block;
    }

    &__tabs-content{
        padding-top: 45px;
    }

    &__tabs-item{
        display: none;
    }

    &__tabs-item.-js-active{
        display: block;
    }

    &__tabs-item-content p,
    &__tabs-item-content li{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px; /* 150% */
    }

    &__tabs-item-content li{
        list-style: none;
        margin-bottom: 16px;
        position: relative;
        padding-left: 21px;
    }

    &__tabs-item-content ul li::before{
        content: '';
        position: absolute;

        width: 6px;
        height: 6px;
        border-radius: 50%;

        left: 0;
        top: 8px;

        background: #F27FFF;
    }

    &__tabs-item-content li:last-child{
        margin-bottom: 0;
    }

    &__tabs-item-content ul{
        padding-left: 20px;
    }

    &__tabs-item-content h3{
        color: #160052;
        font-family: Days One;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__tabs-list{
        margin-top: 40px;
    }

    &__tabs-list h3{
        margin-bottom: 20px;
    }
}

@media(max-width: 1440px) {
    .product-info{
        &__container{
            padding: 40px 25px 26px 40px;
        }

        &__title{
            font-size: 38px;
        }
        
        &__price{
            flex-direction: column;
            align-items: flex-start;
            gap: 8px;
        }

        &__current-price{
            font-size: 28px;
        }

        &__tabs{
            padding: 30px 40px;
        }

        &__tabs-item-content h3{
            font-size: 20px;
        }

        &__tabs-item-content p,
        &__tabs-item-content li {
            font-size: 14px;
        }

        &__tabs-item-content li{
            margin-bottom: 10px;
        }

        &__tabs-item-content ul{
            padding-left: 10px;
        }

        &__tabs-list{
            margin-top: 30px;
        }

        &__tabs-content{
            padding-top: 30px;
        }
    }
}

@media(max-width: 1200px) {
    .product-info{
        margin-bottom: 100px;

        &__container{
            padding: 25px;
            gap: 30px;
        }

        &__title{
            font-size: 36px;
        }

        &__tabs{
            padding: 25px;
        }

        &__current-price{
            font-size: 26px;
        }

        &__old-price{
            font-size: 16px;
        }

        &__tabs-button{
            font-size: 18px;
        }

        &__tabs-button::before{
            bottom: -23px;
        }

        &__tabs-buttons{
            padding-bottom: 20px;
        }

        &__form-submit{
            max-width: 240px;
        }

        &__form-submit-text{
            font-size: 16px;
        }

        &__content::after{
            right: -100px;
        }

    }
}

@media(max-width: 1024px) {
    .product-info{
        &__container{
            grid-template-columns: 1fr;
            grid-gap: 20px;
            padding: 20px;
        }

        &__title{
            font-size: 34px;
            margin-bottom: 15px;
        }

        &__wrapper{
            padding-bottom: 15px;
            margin-bottom: 15px;
        }

        &__tabs-item-content h3{
            font-size: 18px;
            margin-bottom: 15px;
        }

        &__tabs{
            padding: 20px;
        }

        &__form-submit{
            height: 60px;
        }

        &__content::after{
            bottom: -24px;
        }

        &__main-image{
            height: 440px;
        }

        &__slider{
            gap: 20px;
        }
    }
}

@media(max-width: 768px) {
    .product-info{
        margin-bottom: 80px;

        &__title{
            font-size: 32px;
        }

        &__tabs-button{
            font-size: 16px;
        }

        &__tabs-item-content h3{
            font-size: 16px;
            margin-bottom: 10px;
        }

        &__tabs-list{
            margin-top: 20px;
        }

        &__current-price{
            font-size: 24px;
        }

        &__thumbs-slider{
            height: 300px;
        }

        &__thumb-image{
            height: 90px;
        }

        &__thumbs-button--prev{
            top: 20px;
        }

        &__thumbs-button--next{
            bottom: 20px;
        }

        &__main-image{
            height: 400px;
        }

        &__main-slider{
            max-width: 360px;
        }
    }
}

@media (max-width: 576px) {
    .product-info{
        &__characteristics{
            max-width: 100%;
        }

        &__characteristics-value{
            max-width: unset;
            width: auto;
        }

        &__text{
            max-width: 100%;
        }

        &__title{
            font-size: 30px;
        }
    }
}

@media(max-width: 475px) {
    .product-info{
        margin-bottom: 60px;

        &__slider{
            flex-direction: column;
            align-items: center;
            gap: 10px;
        }

        &__thumbs-wrapper{
            width: 100%;
            height: auto;
            flex-shrink: 1;
        }

        &__thumbs-slider{
            height: 100%;
            max-width: 210px;
        }

        &__main-slider{
            order: -1;
            max-width: 240px;
        }

        &__thumbs-button{
            top: 50%;
            transform: translateY(-50%) rotate(-90deg);
            left: auto;
            
        }

        &__thumbs-button--prev{
            left: 0;
        }

        &__thumbs-button--next{
            right: 0;
        }

        &__main-image{
            height: 260px;
        }

        &__thumb-image{
            width: 60px;
            height: 60px;
        }

        &__title{
            font-size: 28px;
        }

        &__form-submit-text{
            display: none;
        }

        &__form-submit{
            max-width: 60px;
        }

        &__content::after{
            right: -275px;
        }

        &__tabs-button::before{
            bottom: -6px;
        }

        &__characteristics-item{
            gap: 10px;
        }

        &__characteristics-title,
        &__characteristics-value {
            font-size: 12px;
        }

        &__container{
            padding: 15px;
        }

        &__tabs{
            padding: 15px;
        }

        &__current-price{
            font-size: 22px;
        }

        &__tabs-item-content ul{
            padding: 0;
        }

        &__tabs-item-content li{
            padding-left: 15px;
        }

        &__tabs-content{
            padding-top: 25px;
        }
    }
}