.cart{
    margin-bottom: 130px;
    margin-top: 15px;

    &__wrapper{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 50px;
    }

    &__content{
        max-width: 820px;
        width: 100%;
    }

    &__title{
        color: #160052;
        font-family: Days One;
        font-size: 48px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__reset-all{
        display: flex;
        align-items: center;
        gap: 8px;

        cursor: pointer;
        outline: none;
        background: transparent;
        padding: 0;
        margin: 0;
        border: none;

        transition: opacity .4s ease;
    }

    &__reset-all:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__reset-all-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__reset-all-text{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 13px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 184.615% */
    }
}

.cart-content{
    &__top{
        margin-bottom: 40px;

        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }
    
    
}

.cart-item{
    display: flex;
    align-items: center;
    gap: 30px;

    padding: 30px 0;

    border-top: 1px dashed rgba(22, 0, 82, 0.2);

    &:last-child{
        border-bottom: 1px dashed rgba(22, 0, 82, 0.2);
    }

    &__image{
        width: 100px;
        height: 100px;
    }

    &__image img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__wrapper{
        flex: 1;
    }

    &__wrapper-top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 30px;
    }

    &__title{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 120% */
    }

    &__remove{
        display: flex;
        align-items: center;
        gap: 5px;

        height: 26px;

        padding: 0 10px;
        border-radius: 50px;
        background: rgba(22, 0, 82, 0.05);

        outline: none;
        cursor: pointer;
        margin: 0;
        border: none;

        transition: opacity .4s ease;
    }

    &__remove:hover{
        opacity: .7;
        transition: opcity .4s ease;
    }

    &__remove-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__remove-text{
        color: rgba(22, 0, 82, 0.4);
        font-family: Nunito, sans-serif;
        font-size: 11px;
        font-style: normal;
        font-weight: 700;
        line-height: 1; /* 218.182% */
    }

    &__wrapper-bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }

    &__available{
        display: flex;
        align-items: center;
        gap: 8px;
    }

    &__available-icon {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__available-text{
        color: #60AD45;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px; /* 200% */
    }

    &__counter{
        display: flex;
        align-items: center;
        gap: 15px;
    }

    &__counter-wrapper{
        display: flex;
        align-items: center;
    }

    &__counter-button{
        display: flex;
        align-items: center;
        justify-content: center;

        outline: none;
        cursor: pointer;
        background: transparent;
        padding: 0;
        margin: 0;
        border: none;

        transition: opacity .4s ease;
    }

    &__counter-button:hover{
        opacity: .7;
        transition: opacity .4s ease;
    }

    &__counter-value{
        height: 22px;
        max-width: 40px;

        display: block;

        outline: none;
        padding: 0;
        border: none;
        background: transparent;

        color: #160052;
        text-align: center;
        font-family: Nunito, sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: 22px; /* 122.222% */
    }

    &__counter-label{
        color: #160052;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 1;
    }

    &__price{
        display: flex;
        align-items: center;
        gap: 23px;
    }

    &__old-price{
        color: #160052;
        font-family: Days One;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        position: relative;
    }

    &__old-price::before{
        content: '';
        position: absolute;

        left: 50%;
        top: 50%;    
        transform: translate(-50%, -50%);

        height: 1px;
        width: 110%;
        background: #F27FFF;
    }

    &__current-price{
        color: #160052;
        font-family: Days One;
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.cart-aside{
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 50px 50px 40px;

    border-radius: 20px;
    border: 3px solid #FBFB00;
    background: #FFF;

    position: sticky;
    top: 0;

    max-width: 460px;
    width: 100%;

    &__title{
        color: #160052;
        text-align: center;
        font-family: Days One;
        font-size: 30px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        margin-bottom: 30px;
    }

    &__price{
        height: 150px;
        width: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        border-radius: 15px;
        border: 2px dashed rgba(22, 0, 82, 0.2);

        margin-bottom: 40px;
    }

    &__total-price{
        color: #F27FFF;
        font-family: Days One;
        font-size: 36px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px; /* 61.111% */

        margin-bottom: 18px;
    }

    &__sale-price{
        color: #160052;
        font-family: Days One;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        position: relative;
    }

    &__sale-price::after{
        content: '';
        position: absolute;

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        width: 110%;
        height: 1px;

        background: #F27FFF;
    }

    &__button{
        width: 100%;
        height: 70px;

        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;

        outline: none;
        cursor: pointer;
        padding: 0;
        border: none;

        border-radius: 40px;
        background: #FBFB00;

        margin-bottom: 20px;

        transition: opacity .4s ease;
    }

    &__button:hover{
        transition: opacity .4s ease;
        opacity: .7;
    }

    &__button-text{
        color: #160052;
        font-family: Days One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__button-icon{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__warning{
        color: #160052;
        text-align: center;
        font-family: Nunito, sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; /* 200% */
    }
}

@media(max-width: 1440px) {
    .cart{
        &__title{
            font-size: 44px;
        }
        
    }

    .cart-content{
        &__top{
            margin-bottom: 30px;
        }
    }

    .cart-aside{
        padding: 30px;

        max-width: 380px;

        &__title{
            font-size: 26px;
        }

        &__price{
            margin-bottom: 30px;
        }

        &__total-price{
            font-size: 30px;
            margin-bottom: 12px;
        }

        &__button-text{
            font-size: 16px;
        }

        &__button{
            height: 60px;
        }
    }
}

@media(max-width: 1200px) {
    .cart{
        margin-bottom: 100px;

        &__title{
            font-size: 40px;
        }
    }

    .cart-content{
        &__top{
            margin-bottom: 20px;
        }
    }

    .cart-item{
        padding: 20px 0;

        &__price{
            gap: 2px;
            flex-direction: column;
            align-items: flex-end;
        }

        &__current-price{
            font-size: 20px;
            order: -1;
        }
    }

    .cart-aside{
        max-width: 320px;
        padding: 20px;

        &__title{
            font-size: 22px;
        }

        &__price{
            height: 130px;
        }

        &__total-price{
            font-size: 26px;
            margin-bottom: 8px;
        }

        &__sale-price{
            font-size: 20px;
        }
    }
}

@media(max-width: 991px) {
    .cart{
        &__wrapper{
            flex-direction: column;
            align-items: center;
        }
    }

    .cart-aside{
        order: -1;
        max-width: 100%;

        position: static;
    }
    
    .cart-content{
        max-width: 100%;
    }
}

@media(max-width: 768px) {
    .cart{
        margin-bottom: 80px;

        &__title{
            font-size: 38px;
        }
    }

    .cart-aside{
        &__title{
            margin-bottom: 25px;
        }

        &__price{
            margin-bottom: 25px;
        }
    }

    .cart-item{
        gap: 15px;

        &__title{
            font-size: 18px;
        }

        &__wrapper-top{
            margin-bottom: 20px;
        }

        &__current-price{
            font-size: 18px;
        }

        &__image{
            width: 80px;
            height: 80px;
        }

        &__counter-label{
            display: none;
        }
    }
}

@media(max-width: 475px) {
    .cart{
        margin-bottom: 60px;

        &__title{
            font-size: 32px;
        }
    }

    .cart-item{

        flex-direction: column;
        align-items: flex-start;

        &__image{
            width: 60px;
            height: 60px;
        }

        &__title{
            font-size: 16px;
            line-height: 20px;
        }

        &__wrapper-bottom{
            flex-wrap: wrap;
            gap: 20px 30px;
        }

        &__price{
            align-items: flex-start;
        }
    }

    .cart-aside{
        &__total-price{
            font-size: 24px;
        }

        &__button{
            height: 50px;
        }

        &__button-text{
            font-size: 14px;
        }
    }
}